@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.piano{font-size:12px;font-weight:400;display:-webkit-flex;display:flex;padding:18px 32px;border-radius:8px 8px 12px 12px;background:var(--keyboard);width:calc(100% - 100px);margin:60px 50px;margin-top:0;box-shadow:5px 5px 0 var(--keyboard-shadow)}.piano-octave{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:100%;height:var(--keyboard-white-height);display:block;position:relative}.piano-octave-white,.piano-octave-black{display:-webkit-flex;display:flex;width:100%;height:100%;top:0;left:0;position:absolute;-webkit-align-items:baseline;align-items:baseline}.piano-octave-black{height:var(--keyboard-black-height)}.piano button{outline:none;transition:all 100ms ease-out;margin:0px;padding:0px;color:var(--keyboard-label);border-radius:0px 0px 4px 4px;border:1px solid var(--keyboard)}.piano button.white{background:var(--keyboard-white);width:100%;height:100%;box-shadow:inset var(--keyboard-white-shade) 0 -10px 0 0;padding-top:80px}.piano button.white:active,.piano button.white.active{background:var(--keyboard-white-pressed);box-shadow:inset var(--keyboard-white-shade) 0 -2px 0 0;-webkit-transform:translate(0, 4px);transform:translate(0, 4px);height:calc(var(--keyboard-white-height) - 8px)}.piano button.black{border:2px solid var(--keyboard-mode);background:var(--keyboard-black);-webkit-flex-grow:1;flex-grow:1;height:var(--keyboard-black-height);z-index:99;-webkit-transform:translateY(-10px);transform:translateY(-10px);box-shadow:inset var(--keyboard-black-shade) 0 -10px 0 0;margin:0 1px}.piano button.black.half{-webkit-flex-grow:0.5;flex-grow:0.5}.piano button.black.hidden{visibility:hidden}.piano button.black:active,.piano button.black.active{background:var(--keyboard-black-pressed);height:calc(var(--keyboard-black-height) - 8px);box-shadow:inset var(--keyboard-black-shade) 0 -2px 0 0}

.piano-waterfall{-webkit-flex-grow:1;flex-grow:1;width:calc(100% - 164px);height:100%;position:relative;display:-webkit-flex;display:flex;margin:5px calc(50px + 32px);margin-bottom:5px;background:var(--waterfall)}.piano-waterfall-octave{-webkit-flex-grow:1;flex-grow:1;outline:1px solid red;position:relative}.piano-waterfall-octave-white,.piano-waterfall-octave-black{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:100%;height:100%;position:absolute;top:0;left:0}.piano-waterfall-octave-black{display:none}.piano-waterfall-column{-webkit-flex-grow:1;flex-grow:1;outline:1px solid blue;position:relative}.piano-waterfall-column.half{-webkit-flex-grow:0.5;flex-grow:0.5}.piano-waterfall-column.hidden{visibility:hidden}.piano-waterfall-note{position:absolute;background-color:black;width:100%}

.menu-bar{padding:10px 20px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.menu-bar .menu-item{background-color:transparent;border:none;width:40px;height:40px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;cursor:pointer;transition:all 200ms}.menu-bar .menu-item svg{opacity:0.8}.menu-bar .menu-item:hover svg{opacity:1}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Open Sans", sans-serif}:root{--button-color: #383838;--rec-button-color: #eb3939;--text-color: #383838;--background: #f0f0f0;--waterfall: #cecece;--keyboard: #9b080e;--keyboard-shadow: #630508;--keyboard-white: #f7fff7;--keyboard-white-shade: #d9dfd9;--keyboard-white-pressed: #d3d3d3;--keyboard-black: #292929;--keyboard-black-shade: #181818;--keyboard-black-pressed: #111111;--keyboard-label: rgba(--keyboard-black, 0.5);--keyboard-white-height: 120px;--keyboard-black-height: 80px}html,body,#root,.App{width:100%;height:100%}.App{font-family:sans-serif;text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background-color:#f0f0f0;background-color:var(--background);padding:4px}.App.recording{padding:0;border:4px solid #eb3939;border:4px solid var(--rec-button-color)}header{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:20px}header p{padding:0 26px;font-size:24px;color:#383838;color:var(--text-color)}header p.latency{font-size:14px}

